.purchase-orders {
  padding: 1.5rem;
  background-color: #f9fafb;
  min-height: 100vh;
}
.purchase-orders .overview-cards-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.purchase-orders .overview-cards-row .overview-card {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  position: relative;
}
.purchase-orders .overview-cards-row .overview-card .card-content .card-value {
  font-size: 2rem;
  font-weight: 700;
  color: #111827;
  line-height: 1;
  margin-bottom: 0.5rem;
}
.purchase-orders .overview-cards-row .overview-card .card-content .card-label {
  font-size: 1rem;
  color: #374151;
  font-weight: 600;
}
.purchase-orders .overview-cards-row .overview-card .card-icon {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  width: 2rem;
  height: 2rem;
  color: #9ca3af;
}
.purchase-orders .overview-cards-row .overview-card.total-pos .card-icon {
  color: #3b82f6;
}
.purchase-orders .overview-cards-row .overview-card.pending .card-icon {
  color: #f59e0b;
}
.purchase-orders .overview-cards-row .overview-card.approved .card-icon {
  color: #10b981;
}
.purchase-orders .overview-cards-row .overview-card.total-value .card-icon {
  color: #8b5cf6;
}
.purchase-orders .control-bar {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}
.purchase-orders .control-bar .search-controls {
  display: flex;
  gap: 1rem;
  align-items: center;
  flex: 1;
  min-width: 300px;
}
.purchase-orders .control-bar .search-controls .search-container {
  position: relative;
  flex: 1;
}
.purchase-orders .control-bar .search-controls .search-container .search-icon {
  position: absolute;
  left: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1.25rem;
  height: 1.25rem;
  color: #9ca3af;
}
.purchase-orders .control-bar .search-controls .search-container .search-input {
  width: 100%;
  padding: 0.75rem 0.75rem 0.75rem 2.5rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  background-color: #f9fafb;
}
.purchase-orders .control-bar .search-controls .search-container .search-input:focus {
  outline: none;
  border-color: #3b82f6;
  background-color: white;
}
.purchase-orders .control-bar .search-controls .status-dropdown {
  position: relative;
  min-width: 150px;
}
.purchase-orders .control-bar .search-controls .status-dropdown .status-select {
  width: 100%;
  padding: 0.75rem 2.5rem 0.75rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  background-color: white;
  appearance: none;
  cursor: pointer;
}
.purchase-orders .control-bar .search-controls .status-dropdown .status-select:focus {
  outline: none;
  border-color: #3b82f6;
}
.purchase-orders .control-bar .search-controls .status-dropdown .dropdown-arrow {
  position: absolute;
  right: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1rem;
  height: 1rem;
  color: #6b7280;
  pointer-events: none;
}
.purchase-orders .control-bar .action-buttons {
  display: flex;
  gap: 0.75rem;
  align-items: center;
  flex-wrap: wrap;
}
.purchase-orders .control-bar .action-buttons .btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
  white-space: nowrap;
}
.purchase-orders .control-bar .action-buttons .btn.btn-secondary {
  background-color: white;
  color: #374151;
  border: 1px solid #d1d5db;
}
.purchase-orders .control-bar .action-buttons .btn.btn-secondary:hover {
  background-color: #f9fafb;
  border-color: #9ca3af;
}
.purchase-orders .control-bar .action-buttons .btn.btn-primary {
  background-color: #3b82f6;
  color: white;
}
.purchase-orders .control-bar .action-buttons .btn.btn-primary:hover {
  background-color: #2563eb;
}
.purchase-orders .control-bar .action-buttons .btn svg {
  width: 1rem;
  height: 1rem;
}
.purchase-orders .empty-state {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 4rem 2rem;
  text-align: center;
  margin-bottom: 2rem;
}
.purchase-orders .empty-state .empty-icon {
  width: 4rem;
  height: 4rem;
  color: #9ca3af;
  margin: 0 auto 1.5rem auto;
}
.purchase-orders .empty-state .empty-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #111827;
  margin: 0 0 0.75rem 0;
}
.purchase-orders .empty-state .empty-description {
  font-size: 1rem;
  color: #6b7280;
  margin: 0 0 2rem 0;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}
.purchase-orders .empty-state .btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}
.purchase-orders .empty-state .btn:hover {
  background-color: #2563eb;
}
.purchase-orders .empty-state .btn svg {
  width: 1rem;
  height: 1rem;
}

@media (max-width: 768px) {
  .purchase-orders {
    padding: 1rem;
  }
  .purchase-orders .overview-cards-row {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1rem;
  }
  .purchase-orders .overview-cards-row .overview-card {
    padding: 1rem;
  }
  .purchase-orders .overview-cards-row .overview-card .card-content .card-value {
    font-size: 1.5rem;
  }
  .purchase-orders .overview-cards-row .overview-card .card-content .card-label {
    font-size: 0.875rem;
  }
  .purchase-orders .overview-cards-row .overview-card .card-icon {
    width: 1.5rem;
    height: 1.5rem;
  }
  .purchase-orders .control-bar {
    padding: 1rem;
    flex-direction: column;
    align-items: stretch;
  }
  .purchase-orders .control-bar .search-controls {
    flex-direction: column;
    min-width: auto;
  }
  .purchase-orders .control-bar .search-controls .search-container {
    min-width: auto;
  }
  .purchase-orders .control-bar .search-controls .status-dropdown {
    min-width: auto;
  }
  .purchase-orders .control-bar .action-buttons {
    justify-content: stretch;
  }
  .purchase-orders .control-bar .action-buttons .btn {
    flex: 1;
    justify-content: center;
  }
  .purchase-orders .empty-state {
    padding: 2rem 1rem;
  }
  .purchase-orders .empty-state .empty-icon {
    width: 3rem;
    height: 3rem;
  }
  .purchase-orders .empty-state .empty-title {
    font-size: 1.25rem;
  }
  .purchase-orders .empty-state .empty-description {
    font-size: 0.875rem;
  }
}
@media (max-width: 480px) {
  .purchase-orders .overview-cards-row {
    grid-template-columns: 1fr;
  }
  .purchase-orders .control-bar .action-buttons {
    flex-direction: column;
  }
  .purchase-orders .control-bar .action-buttons .btn {
    width: 100%;
  }
}
